<template>
  <div class="ma-1">
    <center>

      <v-card class="mx-auto" elevation="1" max-width="450">
        <v-img width="40%" style="margin: 3px;" :src="logoJcrim"></v-img>
        <v-card-text style="cursor:default" class="py-2 font-weight-black">
          <h2 class="pa-3">JEFATURA DE CRIMINALÍSTICA EL ORO</h2>
          <h3 class="pa-1">TURNOS PERICIAS</h3>
          <h4>EXHIBICIÓN AVA</h4>
        </v-card-text>
        <br>
        <v-card-text>
          <v-text-field @keydown.enter="goTo('Agenda')" style="font-size: large;" class="centered-input" v-model="user" variant="outlined" label="Identificacion" outlined></v-text-field>
          <v-btn :disabled="user.length<4"  @click="goTo('Agenda')" class="mb-4 pa-8" block color="green" size="x-large">
            <h2>INGRESAR</h2>
          </v-btn>
        </v-card-text>
      </v-card>
    </center>

  </div>
</template>
<script>
export default {
  components: {
    
  },
  name: "home",
  data() {
    return {
      logoJcrim: require("@/assets/default/imgJcrim.png"),
user:""
    };
  },
  mounted() {
  },
  created() {
    this.$func.openDialog = this.openDialog;
    this.openDialog("news", { name: "dfdf" });
  },
  computed: {},
  methods: {
    goTo(aux) {
      if (this.user.length<4) {
        return
      }
      this.$user.id=this.user
      this.$router.push({ name: aux })
    },
    contact() {
      window.open(this.url, "_blank");
    },
    close() {
      this.dialog.news = false;
    },
    openDialog(aux, data) {
      this.dialog.data = data;
      this.close();
      switch (aux) {
        case "news":
          this.dialog.title = "Noticias";
          this.dialog.news = true;
          break;
        case "business":
          this.dialog.title = "Negocios";
          this.dialog.business = true;
          break;
        case "login":
          this.dialog.title = "Ingresar / Iniciar Sesión";
          this.dialog.login = true;
          break;
        default:
          break;
      }
      this.dialog.active = true;
    },
  },
};
</script>

<style scoped>
.centered-input >>> input {
  text-align: center
}
.centerFull {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.textTitle {
  font-size: 30px;
}

.textSubtitle {
  font-size: 100px;
}

.startView {
  width: 100%;
}

.col1 {
  background-color: rgba(255, 255, 255, 0.733);
  border-radius: 4%;
}

.col2 {
  background-color: rgba(255, 255, 255, 0.733);
  border-radius: 2%;
  left: 10px;
  position: relative;
}
</style>
